<template>
  <section class="payplugUiBlock generalBlock -login" data-e2e-name="generalLogin">
	<AtomLoader v-if="loading" class="component-wrapper" />
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <form class="_loginForm">
        <InputLabel :inputLabelClass="'_email'" :inputLabelType="'email'" :inputLabelName="'payplug_email'" :inputLabelPlaceholder="email_placeholder" :inputLabelLabel="email_label"  inputLabelData="payplugEmail"/>
        <InputLabel :inputLabelClass="'_password'" :inputLabelType="'password'" :inputLabelName="'payplug_password'" :inputLabelPlaceholder="password_placeholder" :inputLabelLabel="password_label"  inputLabelData="payplugPassword"/>
        <div>
          <PayplugButton :class="disabled_button ? '-disabled' : ''" class="payplugUIButton _connexion" :buttonText="connect" @click.prevent="doLogin" data-e2e-name="payplugConnexion"/>
          <PayplugButton class="payplugUIButton _subscribe -tertiary" :buttonText="not_registered" @click="showRegistration" data-e2e-name="payplugShowRegistration"/>
        </div>
        <div>
          <Anchor v-if="forget_password" class="_forgotPassword" :anchorHref="forget_password.url" :anchorTarget="forget_password.target" :anchorTitle="forget_password.text" :anchorText="forget_password.text" data-e2e-name="payplugForgotPassword"/>
        </div>
      </form>
    </div>
    <Modal ref="errorModal">
      <div>{{this.error_message}}</div><br/>
      <PayplugButton buttonText="OK" @click.prevent="closeModlal" />
    </Modal>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import InputLabel from "../molecules/MoleculeInputLabel";
import PayplugButton from "../atoms/AtomButton";
import Anchor from "../atoms/AtomAnchor";
import { login } from "@/store/ajax";
import Modal from "@/components/molecules/MoleculeModal";
import {store} from "@/store/store";
import AtomLoader from "@/components/atoms/AtomLoader"

export default {
	name: "OrganismSectionLogin",
	components: {BlockHeader, InputLabel, PayplugButton, Anchor, Modal, AtomLoader},
	computed:{
		title: {
			get: () => store.translations.login.title
		},
		description: {
			get: () => store.translations.login.description
		},
		email_label: {
			get: () => store.translations.login.email_label
		},
		email_placeholder: {
			get: () => store.translations.login.email_placeholder
		},
		password_label: {
			get: () => store.translations.login.password_label
		},
		password_placeholder: {
			get: () => store.translations.login.password_placeholder
		},
		not_registered: {
			get: () => store.translations.login.not_registered
		},
		connect: {
			get: () => store.translations.login.connect
		},
		forget_password: {
			get: () => store.translations.login.link_forgot_password
		},
		error_message(){
			if( this.hasError && store.error.login !== null && typeof store.error.login !== "undefined")
				this.showError();

			return store.error.login
		},
		loading:{
			get: () => store.settings.loading.login
		}
	},
	data() {
		return {
			"disabled_button": false,
			hasError: false
		}
	},
	mounted(){
		this.hasError = true;
	},
	methods: {
		showRegistration() {
			this.$emit('notRegisted', true)
		},
		doLogin() {
			store.settings.loading.login = true;
			login();
		},
		showError(){
			this.$refs.errorModal.showModal();
		},
		closeModlal() {
			this.$refs.errorModal.closeModal();
		}
	}
}
</script>

